html {
  box-sizing: border-box;
}
body {
  background:  #f73100;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #8d77c9, #0702589d);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #0e0720be,  #0e0720be, #0e0a69d5); 
  font-family: Arial, Helvetica, sans-serif;
}
ul {
  list-style: none;
  padding: none;
  margin: none;
}
span.resultado {
  position: relative;
  width: 100%;
}
.resultado-enter {
  transition: .5s;
}
.resultado-enter.resultado-enter-active{
  transform: translateY(0%);
}
.resultado-exit{
  transform: translateY(0);
  transition: .5s;
  position : absolute;
  left: 0;
  bottom : 0;
  right: 0;
}
.resultado-exit-active{
  opacity: 0;
  transform: translateY(-10%);
}